body{
    background: orange;
}

.container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}
.container > span {
    grid-column: 2/3;
    text-align: center;
}

.foodWrapper{
    grid-column: 2/3;
}
.foods{
    grid-column: 1/3;
    display: grid;
    grid-template-columns: 2fr 1fr;
}
.foods > div {
    grid-column: 1/2;
}
.foods > button {
    grid-column: 2/3;
}
.container > form {
    grid-column: 2/3;
    text-align: center;
}